/*
** $mainColor: #1fa67a; //rgba(31, 166, 122, 1)
** $darkGreen: #116839; //rgba(17, 104, 57, 1)
**
*/

#root{
    width: 757px;
    margin: auto;
}

/*=============================================
                    HEADER
 ==============================================*/
.header{
    text-align: center;
    margin: 20px auto;
}

.header a{
    color: #fff;
    background: #116839;
    padding: 10px;
    text-decoration: none;
    margin-left: 1px;
}

.header a.active {
    background: #1fa67a;
    border: 1px solid #116839;
}

/*=============================================
                   ORGANISATION
 ==============================================*/
.organisation img{
   width: 100%;
}


/*=============================================
                   AGENDA
 ==============================================*/
 .agenda p {
     text-align: center;
     font-weight: bold;
    color: #116839;
    text-align: center;
    border: 1px solid;
    padding: 5px;
 }
.w3-table th, .agenda  tr.table-header, .agenda  tr.table-header:nth-child(odd){
   background: #1fa67a !important;
   color: white;
   text-align: center !important;
   font-weight: bold;
   border:  1px solid #116839;
}

.agenda table td:nth-child(odd), .agenda table tr:nth-child(odd) {background: #ccc !important;}
.agenda table td{
    border: 1px solid #116839;
    text-align: center;
    vertical-align: middle;
}

/*=============================================
                   PANELISTES
 ==============================================*/
.intervenants .title{
    text-align: center;
    color: #116839;
    font-weight: bold;
    margin: 30px 0;
    border: 1px solid;
    padding: 5px;
}
.intervenants .panel{
    
}
.intervenants .panel .paneliste{
    padding: 10px 5px;
    background: ghostwhite;
    margin-bottom: 5px;
    border: 1px solid gainsboro;
    -webkit-box-shadow: 0 0 3px;
            box-shadow: 0 0 3px;
}
.intervenants .panel .paneliste img{
    width: 100%;
    height: 170px;
}
.intervenants .panel .paneliste .w3-rest{
    padding-left: 15px;
}
.intervenants .panel .paneliste h4{
    font-size: 16px;
    font-weight: bold;
    color: #116839;
    text-decoration: underline;
    margin: 0 0;
}
.intervenants .panel .paneliste h6{
    font-size: 14px;
    color: gray;
    font-style: italic;
}
.intervenants .panel .paneliste p{
    font-size: 16px;
}
.intervenants .panel .paneliste p span{
    font-weight: bold;
    color: #116839;
    font-size: 14px;
}
.intervenants .panel .paneliste a{
    font-weight: bold;
    color: white;
    text-decoration: none;
    font-style: italic;
    background: #116839 !important;
    padding: 5px;
    font-size: 13px;
    float: right;
    margin-right: 15px;
}
.intervenants .panel .paneliste:hover a{
    background: #1fa67a !important;
}

/*========================================
        PANELSITE
=========================================*/
.paneliste a {
    text-decoration: none;
    color: gray;
    -webkit-transition: all 500ms;
    -o-transition: all 500ms;
    transition: all 500ms;
    /* display: block; */
}
.paneliste a:hover {
    background: gray;
    padding: 5px;
    color: white;
    border-radius: 5px;
    -webkit-transition: all 500ms;
    -o-transition: all 500ms;
    transition: all 500ms;
}
.paneliste img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 4px solid #116839;
    margin-top: 10px;
}
.paneliste img:hover {
    opacity: 0.5;
    cursor: pointer;
}
.paneliste h4 {
    font-size: 16px;
    font-weight: bold;
    color: #116839;
}
.paneliste h6 {
    color: gray;
    font-style: italic;
}

.paneliste iframe{
    width: 80%;
    height: 300px;
}
.paneliste .discours{
    margin-top: 15px;
}

/*========================================
                VIDEOS
=========================================*/
.videos .header {
    text-align: center;
    /*padding: 5px;*/
    color: #116839;
    text-align: center;
    border: 1px solid;
    font-weight: 900;
}

.videos iframe{
    width: 100%;
    height: 300px;
    -webkit-box-shadow: 0 0 5px;
            box-shadow: 0 0 5px;
}
.videos .paneliste {
    text-align: center;
    width: 160px;
    padding: 2px;
    border: 1px solid #116839;
    margin: 10px;
    -webkit-box-shadow: 0 0 5px;
            box-shadow: 0 0 5px;
    float: left;
}
.videos .paneliste img{
    width: 100%;
    height: 150px;
}
.videos .paneliste h4{
    font-size: 13px;
    margin: 5px 0;
}
.videos .paneliste a{
    font-size: 13px;
    background: #116839;
    color: white;
    display: block;
    padding: 3px;
}
.videos .panVideos{
    margin: 0 auto;
}

/*========================================
            RESOLUTIONS
=========================================*/
.resolutions h4 {
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #116839;
    border: 1px solid;
    padding: 5px;
}
.resolutions ol, .resolutions ol {
    font-weight: 900;
    color: #116839;
}
.resolutions ol p, .resolutions ol p {
    font-weight: normal;
    color: black;
}
.resolutions p.withSpan {
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #116839;
    border: 1px solid;
    padding: 5px;
}


